'use client'

import { useState, useEffect } from 'react'
import { Material } from '@/lib/data-context'
import { useData } from '@/lib/data-context'
import { Button } from '@/components/ui/button'
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/components/ui/dialog'
import { Package } from 'lucide-react'

interface MaterialFormProps {
  material?: Material
  isOpen: boolean
  onSubmit: (data: Omit<Material, 'id' | 'lastUpdate'>) => void
  onCancel: () => void
}

export function MaterialForm({ material, isOpen, onSubmit, onCancel }: MaterialFormProps) {
  const [formData, setFormData] = useState({
    name: material?.name || '',
    category: material?.category || '',
    unit: material?.unit || 'unidade',
    quantity: material?.quantity || 0,
    minQuantity: material?.minQuantity || 0,
    supplier: material?.supplier || '',
    unitPrice: material?.unitPrice || 0
  })

  useEffect(() => {
    if (isOpen && material) {
      setFormData({
        name: material.name,
        category: material.category,
        unit: material.unit,
        quantity: material.quantity,
        minQuantity: material.minQuantity,
        supplier: material.supplier,
        unitPrice: material.unitPrice
      })
    } else if (isOpen && !material) {
      setFormData({
        name: '',
        category: '',
        unit: 'unidade',
        quantity: 0,
        minQuantity: 0,
        supplier: '',
        unitPrice: 0
      })
    }
  }, [isOpen, material])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ 
      ...prev, 
      [name]: ['quantity', 'minQuantity', 'unitPrice'].includes(name) ? parseFloat(value) || 0 : value 
    }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit(formData)
  }

  return (
    <Dialog open={isOpen} onOpenChange={(open) => !open && onCancel()}>
      <DialogContent className="sm:max-w-[700px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <div className="flex items-center gap-3">
            <div className="flex h-10 w-10 items-center justify-center rounded-full bg-primary/10">
              <Package className="h-5 w-5 text-primary" />
            </div>
            <DialogTitle className="text-xl">
              {material ? 'Editar Material' : 'Novo Material'}
            </DialogTitle>
          </div>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4 mt-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <label className="text-sm font-medium">Nome do Material</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
              required
            />
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium">Categoria</label>
            <select
              name="category"
              value={formData.category}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
              required
            >
              <option value="">Selecionar categoria</option>
              <option value="escritorio">Material de Escritório</option>
              <option value="escolar">Material Escolar</option>
              <option value="limpeza">Limpeza e Higiene</option>
              <option value="tecnologia">Tecnologia</option>
              <option value="esportes">Esportes e Recreação</option>
              <option value="outro">Outro</option>
            </select>
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium">Unidade de Medida</label>
            <select
              name="unit"
              value={formData.unit}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
            >
              <option value="unidade">Unidade</option>
              <option value="caixa">Caixa</option>
              <option value="resma">Resma</option>
              <option value="metro">Metro</option>
              <option value="quilograma">Quilograma</option>
              <option value="litro">Litro</option>
            </select>
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium">Quantidade em Estoque</label>
            <input
              type="number"
              name="quantity"
              value={formData.quantity}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
              min="0"
              required
            />
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium">Quantidade Mínima</label>
            <input
              type="number"
              name="minQuantity"
              value={formData.minQuantity}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
              min="0"
            />
          </div>

          <div className="space-y-2">
            <label className="text-sm font-medium">Preço Unitário (R$)</label>
            <input
              type="number"
              name="unitPrice"
              value={formData.unitPrice}
              onChange={handleChange}
              className="w-full px-3 py-2 border border-input rounded-md bg-background focus:outline-none focus:ring-2 focus:ring-primary/20 focus:border-primary"
              min="0"
              step="0.01"
              required
            />
          </div>
        </div>

          <DialogFooter className="mt-6">
            <Button type="button" variant="outline" onClick={onCancel}>
              Cancelar
            </Button>
            <Button type="submit" className="bg-primary hover:bg-primary/90">
              {material ? 'Atualizar' : 'Criar'} Material
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}
